@tailwind base;
@tailwind components;
@tailwind utilities;

/* custom avatar */
.user-avatar {
  width: 2.5rem;
  height: 2.5rem;
  background: #007AFF;
  color: white;
  border-radius: 9999px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

/* ensure dark/light root */
html.light {
  --tw-bg-opacity: 1;
}
html.dark {
  --tw-bg-opacity: 1;
}
